@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

/* html init*/
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

/* color variant */
:root {
  --color-bg: #343a40;
  --color-bg-variant: #343a4099;
  --color-white: #fff;
  --color-light: rgba(255, 255, 255, 0.6);

  --color-primary: #007bff;
  --color-primary-variant: #007bff99;
  --color-secondary: #6c757d;
  --color-secondary-variant: #6c757d99;
  --color-success: #28a745;
  --color-success-variant: #28a74599;
  --color-info: #17a2b8;
  --color-info-variant: #17a2b8;
  --color-warning: #ffc107;
  --color-warning-variant: #ffc10799;
  --color-danger: #dc3545;
  --color-danger-variant: #dc354599;
  --color-light: #f8f9fa;
  --color-light-variant: #f8f9fa99;
  --color-dark: #343a40;
  --color-dark-variant: #343a4099;

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: 'Poppins', sans-serif;
  background: var(--color-bg);
  color: var(--color-white);
  line-height: 1.7;
  /* background-image: url(../src/assets/bg-texture.png); */
}

/* GENERAL STYLES */

.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
}

section>h2,
section>h5 {
  text-align: center;
  color: var(--color-light);
}

section>h2 {
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: var(--color-light);
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-white);
}

.btn {
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover {
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: var(--color-primary);
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* MEDIA QUERIES (MEDIUM DEVICE) */

@media screen and (max-width:1024px) {
  .container {
    width: var(--container-width-md);
  }

  section {
    margin-top: 6rem;
  }
}

/* MEDIA QUERIES (SMALL DEVICE) */

@media screen and (max-width:600px) {
  .container {
    width: var(--container-width-sm);
  }

  section>h2 {
    margin-bottom: 2rem;
  }
}